import { Avatar, TableBody, TableCell,Button, TableRow } from "@windmill/react-ui";
import React, { useContext, useState } from "react";
import useToggleDrawer from "hooks/useToggleDrawer";
import StaffDrawer from "components/drawer/StaffDrawer";
import DeleteModal from "components/staff/DeleteStaff";
import ActiveInActiveButton from "components/table/ActiveInActiveButton";
import EditDeleteButton from "components/table/EditDeleteButton";
import Status from "components/table/Status";
import MainDrawer from "components/drawer/MainDrawer";
import { showingTranslateValue } from "utils/translate";
import useFilter from "hooks/useFilter";
import { showDateFormat } from "utils/dateFormate";
import image1 from "../../assets/img/day1.png";
import Tooltip from "components/tooltip/Tooltip";
import { FiEdit, FiTrash2 } from "react-icons/fi";
import { useHistory, useLocation } from "react-router-dom";
import { SidebarContext } from "context/SidebarContext";

const StaffTable = ({ staffs, GetAllStaffList,wholeCount, lang }) => {
  const { title, handleUpdate, isSubmitting, handleResetPassword } =
    useToggleDrawer();
  const history = useHistory();
  const { globalSetting } = useFilter();
  const [serviceId, setserviceId] = useState("");
  const { toggleModal, toggleDrawer1, toggleDrawer, setIsDrawerOpen1 } =
    useContext(SidebarContext);
  const handleUpdate1 = (id) => {
    console.log("clicked");
    history.replace(`/edit-staff/${id}`);
  };
  const handleModalOpen = (data) => {
    toggleModal();
    console.log("clicked---", data);
    setserviceId(data);
  };
  return (
    <>
      <DeleteModal
        id={serviceId}
        title={title}
        GetAllStaffList={GetAllStaffList}
      />

      <TableBody>
        {staffs?.map((staff,i) => (
          <TableRow key={staff._id}>
             <TableCell>
            <span className="text-sm">{(wholeCount?.currentPage-1)*wholeCount?.pageSize+i+1}</span>
            </TableCell> 
            <TableCell>
              <div className="flex items-center">
                <Avatar
                  className="hidden mr-3 md:block bg-gray-50"
                  src={staff.profile_pic}
                  alt="staff"
                />
                <div>
                  <h2 className="text-sm font-medium">
                    {staff?.first_name} {staff?.last_name}
                  </h2>
                </div>
              </div>
            </TableCell>

            <TableCell>
              <span className="text-sm">{staff.Property?.name}</span>
            </TableCell>
            <TableCell>
              <span className="text-sm ">{staff.mobile}</span>
            </TableCell>
            <TableCell>
              <span className="text-sm">
                {"Name : " + staff?.emergency_contact_name}
              </span>
              , <br />
              <span className="text-sm">
                {"Phone : " + staff?.emergency_contact_number}
              </span>
              ,<br />{" "}
              <span className="text-sm">
                {"Relation : " + staff?.emergency_contact_relation}
              </span>
            </TableCell>
            <TableCell>
              <span className="text-sm">{staff?.blood_group}</span>
            </TableCell>
            <TableCell>
              <span className="text-sm">
                {staff?.marital_status ? "married" : "Un married"}
              </span>
            </TableCell>
            <TableCell>
              <span className="text-sm ">{staff?.email}</span>
            </TableCell>
            <TableCell>
              <span className="text-sm ">{staff?.languages_known}</span>
            </TableCell>
            <TableCell>
              <span className="text-sm ">{staff?.postal_code}</span>
            </TableCell>
            <TableCell>
              <span className="text-sm">
                {"Bank Name : " + staff?.bank_name}
              </span>
              , <br />
              <span className="text-sm">
                {"IFSC : " + staff?.bank_ifsc_code}
              </span>
              ,<br />{" "}
              <span className="text-sm">
                {"Branch : " + staff?.bank_branch_name}
              </span>
              ,<br />{" "}
              <span className="text-sm">
                {"Account Number : " + staff?.bank_account_number}
              </span>
            </TableCell>
            <TableCell>
              <span className="text-sm ">{staff?.id_proof_type}</span>
            </TableCell>
            <TableCell>
            <span className="text-sm">  
              <a href={staff?.id_doc_url} target="_blank">
              <Button className="button-style">View</Button>
              </a></span>
            </TableCell>
            <TableCell>
              <span className="text-sm ">{staff?.code}</span>
            </TableCell>
            <TableCell>
              <span className="text-sm ">{staff?.designation}</span>
            </TableCell>
            {/* <TableCell className="text-center">
              <ActiveInActiveButton
                id={staff?.id}
                staff={staff}
                option="staff"
                status={staff.status}
                GetAllStaffList={GetAllStaffList}
              />
            </TableCell> */}

            <TableCell>
              <TableCell>
                <button
                  onClick={() => handleUpdate1(staff?.id)}
                  className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
                >
                  <Tooltip
                    id="edit"
                    Icon={FiEdit}
                    title={"Edit1"}
                    bgColor="#10B981"
                  />
                </button>
                <button
                  onClick={() => handleModalOpen(staff?.id, "title", "product")}
                  className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
                >
                  <Tooltip
                    id="delete"
                    Icon={FiTrash2}
                    title={"Delete"}
                    bgColor="#EF4444"
                  />
                </button>
              </TableCell>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default StaffTable;
