import {
  Button,
  Card,
  CardBody,
  Input,
  Pagination,
  Select,
  Table,
  TableCell,
  TableContainer,
  TableFooter,
  TableHeader,
} from "@windmill/react-ui";
import { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";

//internal import

import useAsync from "hooks/useAsync";
import useFilter from "hooks/useFilter";
import MainDrawer from "components/drawer/MainDrawer";
import StaffDrawer from "components/drawer/StaffDrawer";
import TableLoading from "components/preloader/TableLoading";
import StaffTable from "components/staff/StaffTable";
import NotFound from "components/table/NotFound";
import PageTitle from "components/Typography/PageTitle";
import { AdminContext } from "context/AdminContext";
import { SidebarContext } from "context/SidebarContext";
import AdminServices from "services/AdminServices";
import { Link } from "react-router-dom/cjs/react-router-dom";
import { getAllCommonStaffList } from "services/Api";

const Staff = () => {
  const { state } = useContext(AdminContext);
  const { adminInfo } = state;
  const { toggleDrawer, lang } = useContext(SidebarContext);
  const [loading, setLoading] = useState(true);
  const [staffData, setstaffData] = useState([])
  const [searchName, setsearchName] = useState('')
  const [searchDesigntion, setsearchDesigntion] = useState('')
  const [pagenumber, setpagenumber] = useState(1)
  const [wholeCount, setwholeCount] = useState({})
  const [staffFullData, setstaffFullData] = useState([])
  const { data } = useAsync(() => AdminServices.getAllStaff({ email: adminInfo.email }));
  useEffect(() => {
    GetAllStaffList(pagenumber,searchName,searchDesigntion);
  }, [pagenumber]);
  const GetAllStaffList = async (pagenumber,searchName,searchDesigntion) => {
    const StaffList = await getAllCommonStaffList(pagenumber,searchName,searchDesigntion);
    console.log( StaffList?.data?.data,"getAllCommonStaffList");
    setstaffData(StaffList?.data?.data);
    setstaffFullData(StaffList?.data?.data?.staffs)
    setwholeCount(StaffList?.data?.data?.pagination)
    setLoading(false);
  };
  const handleChangePage = (e) => {
    console.log("clicked",e)
    setpagenumber(e)
    GetAllStaffList(e,searchName,searchDesigntion)
   }
  const {
    userRef,
    setRole,
    totalResults,
    resultsPerPage,
    serviceData,
    handleSubmitUser,
  } = useFilter(staffFullData);

  const { t } = useTranslation();
  const setRoles = async (role)=> {
  setsearchDesigntion(role)
      GetAllStaffList(pagenumber,searchName,role)
  }
  const handleSearch = async (search)=> {
    console.log(search,"search",staffData?.staffs)
      GetAllStaffList(pagenumber,search,searchDesigntion)
  }
  return (
    <>
      <PageTitle>{t("StaffPageTitle")} </PageTitle>
      <MainDrawer>
        <StaffDrawer />
      </MainDrawer>

      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody>
          <form
            onSubmit={handleSubmitUser}
            className="py-3 grid gap-4 lg:gap-6 xl:gap-6 md:flex xl:flex"
          >
            
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                ref={userRef}
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e)=>handleSearch(e.target.value)}
                name="search"
                placeholder={t("Staff Search Names")}
              />
              <button type="submit" className="absolute right-0 top-0 mt-5 mr-1"></button>
            </div>
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Select
                onChange={(e) => setRoles(e.target.value)}
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
              >
                <option value="" defaultValue hidden>
                  {t("StaffRole")}
                </option>
                <option value="">{t("All")}</option>
                <option value="receptionist">{t("Receptionist")}</option>
                <option value="waiter">{t("Waiter")}</option>
                <option value="manager">{t("Manager")}</option>
                <option value="housekeeper">{t("Housekeeper")}</option>
                <option value="accounts">{t("Accounts")}</option>
                
              </Select>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
            <div className="w-full md:w-56 lg:w-56 xl:w-56">
              <Link to="add-staff"> <Button className="w-full rounded-md h-12">
                <span className="mr-3">
                  <FiPlus />
                </span>
                {t("AddStaff")}
              </Button></Link>
            </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        // <Loading loading={loading} />
        <TableLoading row={12} col={7} width={163} height={20} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
              <TableCell>
                {t("S.NO")}
                </TableCell>
                <TableCell>{t("Name")}</TableCell>
                <TableCell>{t("Property")}</TableCell>
                <TableCell>{t("Phone")}</TableCell>
                <TableCell>{'Emergency contact'}</TableCell>
                <TableCell>{'Blood Group'}</TableCell>
                <TableCell>{'Marital status'}</TableCell>
                <TableCell>{'Email ID'}</TableCell>
                <TableCell>{'Languages'}</TableCell>
                <TableCell>{'Postal code'}</TableCell>
                <TableCell>{'Bank Details'}</TableCell>
                <TableCell>{'ID Type'}</TableCell>
                <TableCell>{'ID card'}</TableCell>
                <TableCell>{t("EMp Code")}</TableCell>
                <TableCell>{t("Role")}</TableCell>
                {/* <TableCell className="text-center">{t("Status")}</TableCell> */}
                <TableCell className="text-right">{t("StaffActionsTbl")}</TableCell>
              </tr>
            </TableHeader>
            <StaffTable staffs={staffFullData } GetAllStaffList={GetAllStaffList} wholeCount={wholeCount}/>
          </Table>
          <TableFooter>
            <Pagination
              totalResults={staffData?.pagination?.total}
              resultsPerPage={staffData?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Table navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Sorry, There are no staff right now." />
      )}
    </>
  );
};

export default Staff;
